          <div class="item map">
            <echarts ref="mapRef" :options="mapOptions" />
          </div>
          <div class="item compass">
            <img src="../../assets/img/compass.png" />
          </div>
          <!-- <transition name="fade">
            <div v-if="showRadar" ref="radarDom" class="item radar"></div>
          </transition> -->
          <transition name="fade">
            <div class="item dialog" v-if="showDialog">
