                </span>
              </div>
              <div class="content">
                <span>生物识别异常数据</span>
                <span class="link">
                  <!-- <my-count-to :data="testData" /> -->
                  {{ testData }}
                  <span>条</span>
                </span>
              </div>
              <div class="content">
                <span>进销存异常数据</span>
                <span class="link">
                  <!-- <my-count-to :data="testData" /> -->
                  {{ testData }}
                  <span>条</span>
                </span>
              </div>
            </div>
          </transition>
        </div>
        <div class="right">
          <div class="item item1">
            <quota1
              :icon="require('../../assets/img/box2-icon1.png')"
