              :data="7654321"
              :dataStyle="box2DataStyle"
              suffix="万元"
              :suffixStyle="box2SuffixStyle"
            />
            <quota1
              :icon="require('../../assets/img/box2-icon1.png')"
              title="普通门诊累计费用"
              :data="7654321"
              :dataStyle="box2DataStyle"
              suffix="万元"
              :suffixStyle="box2SuffixStyle"
            />
            <quota1
              :icon="require('../../assets/img/box2-icon1.png')"
              title="基金累计消耗"
              :data="7654321"
              :dataStyle="box2DataStyle"
              suffix="万元"
              :suffixStyle="box2SuffixStyle"
            />
          </div>
          <div class="item item2">
