              <sub class="en">Hospital Details</sub>
            </div>
            <div class="chart-container chart2">
              <div class="chart">
                <echarts :options="hospitalDetailsOptions1" />
              </div>
              <div class="chart">
                <echarts :options="hospitalDetailsOptions2" />
              </div>
              <div class="chart">
                <echarts :options="hospitalDetailsOptions3" />
              </div>
              <div class="chart">
                <echarts :options="hospitalDetailsOptions4" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from "vue";
import * as echarts from "echarts";
import quota1 from "@/components/project/Quota1/Index.vue";
import { time } from "echarts";
export default {
  name: "Home",
  components: {
    quota1,
  },
  setup() {
    const mapRef = ref({});
    // const radarDom = ref(null);
    const showDialog = ref(false);
    // const showRadar = ref(true);
    const quota1Data1 = ref(7654321);
    const testData = ref(8);
    const box2DataStyle = {
      color: "#fcdd02",
      fontSize: "2rem",
      fontWeight: "bold",
    };
    const box2SuffixStyle = {
      color: "#fcdd02",
      fontSize: "1.3rem",
    };

    const percentageOptions = reactive({
      legend: {
        icon: "circle",
        left: "right",
        textStyle: {
          color: "#fff",
          fontSize: 25,
        },
      },
      color: ["#ffd200", "#954eff", "#1c68da"],
      series: [
        {
          type: "pie",
          radius: ["55%", "75%"],
          clockwise: false,
          label: {
            fontSize: 25,
            color: "#fff",
            formatter(params) {
              return `${params.name}\n\n${params.percent}%`;
            },
          },
          labelLine: {
            length: 50,
            length2: 75,
            lineStyle: {
              width: 3,
            },
          },
          data: [
            {
              name: "药品占比",
              value: 40,
            },
            {
              name: "材料占比",
              value: 50,
            },
            {
              name: "诊疗项目占比",
              value: 60,
            },
          ],
        },
      ],
    });

    const hospitalDetailsOptions1 = reactive({
      title: {
        text: "住院在床人数",
        left: "center",
        top: "bottom",
        textStyle: {
          color: "#fff",
          fontSize: 30,
        },
      },
      color: ["#1dc8fc", "rgba(38,50,73,0.2)"],
      series: [
        {
          type: "pie",
          radius: ["55%", "65%"],
          center: ["50%", "50%"],
          data: [
            {
              name: "住院在床人数",
              value: 985,
              label: {
                position: "center",
                fontSize: 40,
                fontWeight: "bold",
                color: "#1dc8fc",
                formatter(params) {
                  return `${params.value}人`;
                },
              },
            },
            {
              name: "空床数",
              value: 500,
              silent: true,
              label: {
                show: false,
              },
            },
          ],
        },
      ],
    });

    const hospitalDetailsOptions2 = reactive({
      title: {
        text: "平均住院日",
        left: "center",
        top: "bottom",
        textStyle: {
          color: "#fff",
          fontSize: 30,
        },
      },
      color: ["#1dc8fc", "rgba(38,50,73,0.2)"],
      series: [
        {
          type: "pie",
          radius: ["55%", "65%"],
          center: ["50%", "50%"],
          data: [
            {
              name: "平均住院日",
              value: 985,
              label: {
                position: "center",
                fontSize: 40,
                fontWeight: "bold",
                color: "#1dc8fc",
                formatter(params) {
                  return `${params.value}天`;
                },
              },
            },
            {
              name: "剩余",
              value: 500,
              silent: true,
              label: {
                show: false,
              },
            },
          ],
        },
      ],
    });

    const hospitalDetailsOptions3 = reactive({
      title: {
        text: "均次住院费用",
        left: "center",
        top: "bottom",
        textStyle: {
          color: "#fff",
          fontSize: 30,
        },
      },
      color: ["#e60012", "rgba(38,50,73,0.2)"],
      series: [
        {
          type: "pie",
          radius: ["55%", "65%"],
          center: ["50%", "50%"],
          data: [
            {
              name: "均次住院费用",
              value: 985,
              label: {
                position: "center",
                fontSize: 40,
                fontWeight: "bold",
                color: "#e60012",
                formatter(params) {
                  return `${params.value}元`;
                },
              },
            },
            {
              name: "剩余",
              value: 500,
              silent: true,
              label: {
                show: false,
              },
            },
          ],
        },
      ],
    });

    const hospitalDetailsOptions4 = reactive({
      title: {
        text: "均次基金支出",
        left: "center",
        top: "bottom",
        textStyle: {
          color: "#fff",
          fontSize: 30,
        },
      },
      color: ["#1dc8fc", "rgba(38,50,73,0.2)"],
      series: [
        {
          type: "pie",
          radius: ["55%", "65%"],
          center: ["50%", "50%"],
          data: [
            {
              name: "均次基金支出",
              value: 985,
              label: {
                position: "center",
                fontSize: 40,
                fontWeight: "bold",
                color: "#1dc8fc",
                formatter(params) {
                  return `${params.value}元`;
                },
              },
            },
            {
              name: "剩余",
              value: 500,
              silent: true,
              label: {
                show: false,
              },
            },
          ],
        },
      ],
    });

    echarts.registerMap("510900", require("@/assets/maps/510900.json"));

    const mapOptions = reactive({
      geo: {
        map: "510900",
        roam: true,
        zoom: 1.2,
        label: {
          show: true,
          color: "#ffffff",
          fontSize: 40,
        },
        itemStyle: {
          borderColor: "#0eaac3",
          borderWidth: 4,
          shadowColor: "rgba(0, 0, 0, 0.5)",
          shadowBlur: 10,
        },
        regions: [
          {
            name: "射洪市",
            itemStyle: {
              areaColor: "#3c5bb7",
            },
          },
          {
            name: "蓬溪县",
            itemStyle: {
              areaColor: "#0d82c7",
            },
          },
          {
            name: "安居区",
            itemStyle: {
              areaColor: "#6c8c8f",
            },
          },
          {
            name: "大英县",
            itemStyle: {
              areaColor: "#656f88",
            },
          },
          {
            name: "船山区",
            itemStyle: {
              areaColor: "#0c947a",
            },
          },
        ],
      },
      series: [
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          // showEffectOn: "emphasis",
          symbol: "circle",
          symbolSize: 20,
          itemStyle: {
            color: "#ffde00",
          },
          label: {
            show: true,
            position: "bottom",
            fontSize: 30,
            color: "#ffde00",
            offset: [0, 20],
            formatter(params) {
              return params.name;
            },
          },
          rippleEffect: {
            scale: 5,
          },
          emphasis: {
            itemStyle: {
              color: "#ff4040",
            },
            label: {
              show: true,
              position: "bottom",
              fontSize: 30,
              color: "#ff4040",
              offset: [0, 20],
              formatter(params) {
                return params.name;
              },
            },
          },
          data: [
            {
              name: "医院1",
              value: [105.603176, 30.537414],
            },
            {
              name: "医院2",
              value: [105.282948, 30.57722],
            },
            {
              name: "医院3",
              value: [105.717585, 30.737272],
            },
            {
              name: "医院4",
              value: [105.349639, 30.361075],
            },
            {
              name: "医院5",
              value: [105.425528, 30.810746],
            },
          ],
        },
      ],
    });

    let i = 0;
    const highlightOneHospital = () => {
      if (mapRef.value && mapRef.value.chart) {
        mapOptions.geo.center = mapOptions.series[0].data[i].value;
        mapRef.value.chart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
        });
        mapRef.value.chart.dispatchAction({
          type: "highlight",
          dataIndex: i++,
        });
        testData.value = Math.ceil(Math.random() * 10);
        if (i > mapOptions.series[0].data.length - 1) {
          i = 0;
        }
        showDialog.value = true;
      }
    };

    const requestData = () => {
      quota1Data1.value = Math.ceil(Math.random() * 90000) + 10000;
      hospitalDetailsOptions1.series[0].data[0].value =
        Math.ceil(Math.random() * 900) + 100;
    };

    let timer;
    onMounted(() => {
      // mapRef.value.$el.addEventListener("mouseover", () => {
      //   showRadar.value = false;
      // });
      // mapRef.value.$el.addEventListener("mouseout", () => {
      //   showRadar.value = true;
      // });
      timer = setInterval(highlightOneHospital, 4000);
      mapRef.value.chart.on("mouseover", (params) => {
        clearInterval(timer);
      });
      mapRef.value.chart.on("mouseout", (params) => {
        timer = setInterval(highlightOneHospital, 4000);
      });
      setInterval(requestData, 8000);
    });

    return {
      // radarDom,
      mapRef,
      showDialog,
      // showRadar,
      quota1Data1,
      box2DataStyle,
      box2SuffixStyle,
      percentageOptions,
      hospitalDetailsOptions1,
      hospitalDetailsOptions2,
      hospitalDetailsOptions3,
      hospitalDetailsOptions4,
      mapOptions,
      testData,
    };
  },
};
</script>

<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  .title {
    width: 100%;
    height: 10%;
    background-image: url(../../assets/img/title-img.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 100% 80%;
  }
  .main {
    width: 100%;
    height: 90%;
    padding: 2rem;
    .top {
      height: 12%;
      padding: 0 10rem;
      background-image: url(../../assets/img/box1.png);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: nowrap;
    }
    .bottom {
      height: 88%;
      padding: 2rem 0 0 0;
      display: flex;
      flex-wrap: nowrap;
      .left {
        width: 60%;
        position: relative;
        .item {
          position: absolute;
          width: 100%;
          height: 100%;
          &.map {
            z-index: 1;
          }
          &.compass {
            z-index: 2;
            right: 1px;
            top: 1px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 10%;
            height: 20%;
            img {
              height: 60%;
            }
          }
          &.radar {
            z-index: 3;
            left: 25%;
            top: 25%;
            width: 50%;
            height: 50%;
          }
          &.dialog {
            width: 25%;
            height: 40%;
            padding: 1% 1% 1% 1%;
            z-index: 4;
            background-image: url(../../assets/img/box4.png);
            background-position: center;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            .header {
              color: #00ffff;
              font-size: 2.5rem;
              text-align: center;
              padding: 2rem 0;
              border: 2px solid;
              border-image: url(../../assets/img/split1.png) 0 0 2 0 stretch;
            }
            .content {
              color: #ffffff;
              font-size: 2rem;
              text-align: center;
              padding: 1rem 0;
              margin: 0 7%;
              display: flex;
              align-items: center;
              justify-content: space-between;
              &:not(:last-child) {
                border: 2px solid;
                border-image: url(../../assets/img/split1.png) 0 0 2 0 stretch;
              }
              .link {
                display: inline-block;
                border-radius: 0.7rem;
                border: 2px solid #5a9df0;
                color: #5a9df0;
                padding: 0.4rem 0.6rem;
                cursor: pointer;
                &:hover {
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
      .right {
        width: 40%;
        .item {
          .box3-title {
            height: 3rem;
            line-height: 3rem;
            .chinese {
              color: #c1f3ff;
              font-size: 2rem;
            }
            .en {
              color: #50b4f2;
              font-size: 1.5rem;
              margin-left: 2rem;
            }
          }
          .chart-container {
            height: calc(100% - 3rem);
            &.chart1 {
              background-image: url(../../assets/img/percentage-bg.png);
              background-position: center center;
              background-repeat: no-repeat;
              background-size: 32%;
            }
            &.chart2 {
              display: flex;
              flex-wrap: nowrap;
              .chart {
                width: 25%;
                height: 100%;
              }
            }
          }
          &.item1 {
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 20%;
            padding: 0 10%;
            background-image: url(../../assets/img/box2.png);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: 106% 100%;
          }
          &.item2 {
            height: calc(40% - 2rem);
            padding: 4% 6%;
            margin-bottom: 2rem;
            background-image: url(../../assets/img/box3.png);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: 100% 100%;
          }
          &.item3 {
            height: 40%;
            padding: 4% 6%;
            background-image: url(../../assets/img/box3.png);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
}
</style>